import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";

import { ReanderField, ReanderSelect } from "../../../components/helpers/field";
import { NumberOnly } from "../../../Validasi/normalize";
import ValidasiMasterJenis from "../../../Validasi/ValidasiMasterJenis.jsx";

const maptostate = (state) => {
  if (state.datamaster.ShowModalDataKadar !== null) {
    return {
      initialValues: {
        kadar: state.datamaster.ShowModalDataKadar.kadar,
        type_kadar: state.datamaster.ShowModalDataKadar.type_kadar,
      },
    };
  }
};
const option = [
  {
    value: "KADAR-CETAK",
    name: "KADAR-CETAK",
  },
  {
    value: "KADAR-BELI",
    name: "KADAR-BELI",
  },
  {
    value: "KADAR-JUAL",
    name: "KADAR-JUAL",
  },
];
class FormModalKadar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      aktif: false,
    };
  }

  componentDidMount() {
    // setTimeout(() => {
    //   document.getElementById("type_kadar").focus();
    // }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        autoComplete="off"
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
      >
        <div className="form-group">
          <Field
            id="type_kadar"
            label="Type Kadar"
            name="type_kadar"
            options={option}
            value={this.props.type_kadar}
            disabled={this.props.isEdit}
            onBlur={() => this.setState({ aktif: true })}
            placeholder={"Silahkan Pilih Type Kadar"}
            component={ReanderSelect}
          />
        </div>
        <Field
          id="kadar"
          tabIndex="1"
          name="kadar"
          component={ReanderField}
          type="text"
          label="Kadar"
          normalize={NumberOnly}
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Kadar"
        />
        
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="2"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.isLoading}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
      </form>
    );
  }
}

FormModalKadar = reduxForm({
  form: "ModalKadar",
  enableReinitialize: true,
  validate: ValidasiMasterJenis,
})(FormModalKadar);
export default connect(maptostate, null)(FormModalKadar);
